* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #2c3e50;
  background-color: #f9fbfc;
  font-family: Segoe UI, Roboto, sans-serif;
  transition: background-color .3s, color .3s;
}

.app-header {
  text-align: center;
  background: #fefefe linear-gradient(#fff, #f9fbfc);
  border-bottom: 2px solid #e0e0e0;
  border-radius: 0 0 12px 12px;
  padding: 24px 32px;
  transition: background-color .3s, box-shadow .3s;
  position: relative;
  box-shadow: 0 2px 6px #0000000f;
}

.heading {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: inline-flex;
}

.app-header h1 {
  color: #007bff;
  margin-bottom: 6px;
  font-size: 28px;
  font-weight: 600;
  transition: color .3s;
}

.logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo i {
  color: #f39c12;
  font-size: 32px;
}

.app-header p {
  color: #666;
  letter-spacing: .3px;
  margin-top: 4px;
  font-size: 15px;
  font-weight: 400;
}

.toggle-btn {
  cursor: pointer;
  background-color: #0000;
  border: 2px solid #007bff;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  padding: 0;
  transition: background-color .3s, border-color .3s, box-shadow .3s;
  display: flex;
  position: absolute;
  top: 24px;
  right: 32px;
}

.toggle-btn:hover {
  background-color: #e6f0ff;
  box-shadow: 0 0 6px #007bff4d;
}

.toggle-btn i {
  color: #007bff;
  font-size: 12px;
  transition: color .3s;
}

.toggle-btn i:hover {
  color: #0056b3;
}

.app-container {
  text-align: center;
  background-color: #f5f7f9;
  border-radius: 12px;
  max-width: 900px;
  margin: 40px auto;
  padding: 32px;
  transition: background-color .3s, box-shadow .3s, color .3s;
  box-shadow: 0 4px 12px #0000000d;
}

.btn, .map-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  margin: 20px 10px;
  padding: 12px 24px;
  font-size: 15px;
  font-weight: 600;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.btn:hover {
  background-color: red;
}

.map-btn:hover {
  background-color: green;
}

.location-info {
  text-align: left;
  background-color: #f1f3f5;
  border-left: 4px solid #007bff;
  border-radius: 8px;
  margin: 20px auto;
  padding: 16px 20px;
  font-size: 15px;
  transition: background-color .3s, border-color .3s, color .3s;
}

.map-container {
  border-radius: 12px;
  margin-top: 20px;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 2px 6px #0000001a;
}

.map-container iframe {
  border: none;
  width: 100%;
  height: 400px;
  transition: opacity .3s;
}

body.dark-mode {
  color: #f0f0f0;
  background-color: #0d0d0d;
}

body.dark-mode .app-header {
  background: #000;
  border-bottom: 2px solid #222;
  box-shadow: 0 2px 4px #ffffff0d;
}

body.dark-mode .heading h1 {
  color: orange;
}

body.dark-mode .logo i {
  color: #007bff;
}

body.dark-mode .app-header p {
  color: #fff;
}

body.dark-mode .app-container {
  background-color: #1a1a1a;
  box-shadow: 0 4px 12px #ffffff0d;
}

body.dark-mode .btn, body.dark-mode .map-btn {
  color: #000;
  background-color: orange;
}

body.dark-mode .btn:hover {
  background-color: tomato;
}

body.dark-mode .map-btn:hover {
  background-color: #32cd32;
}

body.dark-mode .location-info {
  color: #fff;
  background-color: #2c2c2c;
  border-left: 4px solid orange;
}

body.dark-mode .map-container {
  box-shadow: 0 2px 6px #ffffff1a;
}

body.dark-mode .toggle-btn {
  border: 2px solid orange;
}

body.dark-mode .toggle-btn:hover {
  background-color: #333;
  box-shadow: 0 0 6px #ffa5004d;
}

body.dark-mode .toggle-btn i {
  color: orange;
}

@media (width <= 600px) {
  .app-header {
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 12px 16px;
    display: flex;
  }

  .heading {
    align-items: center;
    gap: 6px;
    font-size: 18px;
    display: flex;
  }

  .toggle-btn {
    margin-left: auto;
    position: static;
  }

  .app-header h1 {
    font-size: 20px;
  }

  .app-header p {
    display: none;
  }

  .app-container {
    margin: 20px 10px;
    padding: 20px;
  }

  .btn, .map-btn {
    vertical-align: middle;
    margin: 10px 8px;
  }

  .location-info {
    padding: 12px 16px;
    font-size: 14px;
  }

  .map-container iframe {
    height: 300px;
  }
}
